.image {
  opacity: .6;
  transition: opacity 1s cubic-bezier(.645, .045, .355, 1), filter 1s cubic-bezier(.645, .045, .355, 1);
}

.image img {
  border: 3px solid #ffffff80;
  width: 100%;
  height: auto;
  transition: all 1s cubic-bezier(.645, .045, .355, 1);
}

.image:hover {
  opacity: 1;
}

.image:hover img {
  box-shadow: 0 0 50px 1px #ffffff4d;
}

.image.hidden {
  opacity: 0;
  filter: blur(20px);
}

#imageAladdin {
  align-self: center;
}

#imageEugene, #imageJeanLeon {
  width: 75%;
  margin: 3dvh auto;
}

#history #history2paragraph {
  align-items: end;
  display: flex;
}

#culture .quote h2 {
  width: 75%;
}

#culture .quote .emphasis {
  background-color: #fff0;
}

#beurette .quote {
  justify-content: center;
  display: flex;
}

#beurette .quote h2 {
  width: 100%;
}

#beurette .paragraph-30-70:has(.quote) {
  gap: 3dvw;
}

#analysis p, #analysis h1, #analysis a {
  color: #333030d9;
}

#analysis .read-more:after, #analysis .previous:after {
  background: #333030d9;
}

#analysis .column-count-2 p {
  column-count: 2;
  column-gap: 3dvw;
}

#analysis #analysisTitle {
  margin-top: 5dvh;
}

#men #men1quote {
  width: 75%;
}

#bibliography li::marker {
  font-size: 6pt;
}

#bibliography li {
  color: #fff;
  z-index: 1;
  padding-left: 10px;
  font-family: Didot, serif;
  font-size: min(max(1vw, 12pt), 14pt);
  line-height: min(max(2vw, 24pt), 28pt);
  transition: all 1s cubic-bezier(.645, .045, .355, 1);
}

#bibliography li.hidden {
  opacity: 0;
  filter: blur(20px);
  transform: translateY(50px);
}

#bibliography li .italic {
  font-style: italic;
}

@media screen and (width <= 740px) {
  .image {
    opacity: 1;
  }

  .image img {
    width: 100%;
    height: auto;
  }

  #imageEugene, #imageJeanLeon, #culture .quote h2 {
    width: 100%;
  }

  #imageAladdin {
    margin: 3dvh 0;
  }

  #analysis .column-count-2 p {
    column-count: 1;
    column-gap: 0;
  }

  #men #men1quote {
    width: 100%;
  }
}
